
.footer{
    margin-bottom: 20px;
}.hea{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 50px;
}
.list{
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-evenly;
    align-items: flex-end;
    gap: 80px;
}
.iconsociale{
    display: flex;
    flex-direction: row;
    gap: 20px;
}
.socailicon {
    width: 30px;
    height: 30px;
}
.fini h1{
    font-size: 16px;
    color: gray;
    text-align: center;
    font-weight: 400;
}










@media (max-width: 992px) {
    .hea {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }
    .list {
        display: flex;
        flex-direction: column;
        list-style: none;
        justify-content: space-evenly;
        align-items: center;
        gap: 40px;
    }
    .fini h1 {
        font-size: 13px;
    }
  }