.main {
    background-image: url('../../assets/team.webp');
    background-size: cover;
    background-repeat: no-repeat;
}  
.cintent{
    display: flex;
align-items: center;
flex-direction: column;
padding: 30px;
padding-top: 220px;
padding-bottom: 160px;
}
.title{
    color: white;
    font-size: 90px;
    font-weight: 800;
    text-align: center;
    font-family: Tahoma, Geneva, Verdana, sans-serif;
    width: 1250px;
    line-height: 120px;
    margin: 0;
}
.mainpara{
    color: white;
    font-size: 20px;
    font-weight: 100;
    text-align: center;
    width: 950px;
}
.cintent button{
    background-color: var(--maincollor);
    color: white;
    border: 1px solid var(--maincollor);
    border-radius: 35px;
    padding: 20px 45px;
    font-size: 28px;
    display: flex;
    align-items: center;
    margin-top: 50px;
    cursor: pointer;
}
.icion{
    margin-left: 10px;
}
.image{
    POSITION: absolute;
    left: 376px;
    top: 311px;
}







@media (max-width: 992px) {
    .title {
        font-size: 39px;
        width: 350px;
        line-height: 55px;
    }
    .mainpara {
        font-size: 16px;
        width: 350px;
    }
    .image {
        display: none
    };
  }