.Service{
    padding-top: 100px;
    padding-bottom: 70px;
}
.servises{
    display: flex;
    flex-direction: row;
    gap: 40px;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 35px;
}
.ser_first{
    background-color: #f8f9fa;
    border: 4px solid #dee2e6;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    align-items: center;
    padding: 25px;
    width: 320px;
}
.ser_first:hover{
    border: 4px solid var(--maincollor);
}
.service_icon{
    background-color: #f6cacc9c;
    padding: 15px;
    width: fit-content;
    width: fit-content;
    border-radius: 15px;
}
.info_service p{
    text-align: center;
    color: gray;
}
.info_service{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.info_service h1{
    font-size: 25px;
    font-weight: 700;
    margin: 0;
    margin-top: 28px;
    text-align: center;
}
.service_oara{
    width: 800px !important;
}



@media (max-width: 992px) {
    .service_oara {
        width: 350px !important;
    }
    .servises{
        padding: 0px 25px;
    }

  }