.alladv{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 40px;
    gap: 80px;
    margin-bottom: 30px;
    flex-wrap: wrap;
}
.itemss{
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: -1px 0px 20px 1px rgba(0,0,0,0.50);
    background: white;
    border-radius: 25px;
    padding: 25px;
    width: 200px;
    border: 4px solid white;
}
.itemss:hover{
    border: 4px solid var(--maincollor);
}
.inf{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 25px;
}
.inf h1{
    margin: 0;
    font-weight: 800;
    font-size: 36px;
}
.inf p{
    margin: 0;
    font-size: 16px;
    margin-top: 8px;
}
.imageicon{
    width: 60px;
    height: 60px;
}
.aboutus{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 65px;
}
.aboutus h1{
    color: var(--maincollor);
    font-size: 25px;
    margin: 0;
}
.aboutus h2{
    font-size: 44px;
    margin: 10px;
}
.aboutus p{
    color: gray;
    font-size: 16px;
    width: 700px;
    text-align: center;
}
.lasabout{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 3px solid black;
    border-radius: 25px;
    width: fit-content;
    padding: 18px 45px;
    gap: 50px;
    margin-top: 75px;
    flex-wrap: wrap;
}
.lasabout h1{
    color: black;
    font-size: 32px;
    width: 320px;
    font-weight: 800;
}
.adv{
    background-color: rgba(128, 128, 128, 0.158);
    padding: 8px;
    border-radius: 13px;
}
.first_items p{
    color: gray;
    font-size: 18px;
    width: 150px;
    
}
.first_items{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
}
.smallicon{
    width: 53px;
    height: 53px;
}
.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 130px;
    padding-top: 70px;
}
.coloringdiv{
    background-color: var(--maincollor);
    padding: 200px;
    width: 50%;
    height: 550px;
}
.seccond_info{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.divfor_image{
    background-color: white;
    width: 40%;
}
.mainmage{
    width: 40%;
    height: 90%;
    position: absolute;
    border-radius: 25px;
    left: 235px;
    top: 1640px;
}
.bigtitle{
    MARGIN-LEFT: 197px;
    color: white;
    font-size: 44px;
    font-weight: 700;
    margin-bottom: 60px;
}
.coloringdiv p{
    MARGIN-LEFT: 197px;
    color: white;
    font-size: 18px;
    padding-left: 45px;
    margin-bottom: 40px;
}
.minititle{
    MARGIN-LEFT: 197px;
    color: white;
    font-size: 30px;
    font-weight: 700;
}








@media (max-width: 992px) {
    .aboutus h2 {
        font-size: 29px;
        width: 300px;
        text-align: center;
    }
    .aboutus p {
        width: 350px;
    }
    .alladv {
        gap: 30px;
    }
    .lasabout{
        width: min-content;

    }
.lasabout h1 {
    color: black;
    font-size: 28px;
    width: fit-content;
    text-align: center;
}
.coloringdiv {
    width: 100%;
    height: fit-content;
    padding: 100px 10px;
}
.divfor_image{
    display: none;
}
.bigtitle{
    MARGIN-LEFT: 20px;
    color: white;
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 60px;
    text-align: center;
}
.minititle {
    MARGIN-LEFT: 20px;
    color: white;
    font-size: 26px;
    font-weight: 700;
}
.coloringdiv p {
    MARGIN-LEFT: 20px;
    width: 300px;
}
.mainmage {
opacity: 0
}
  }


  @media (max-width: 700px)
  {
.coloringdiv {
    padding: 100px 5px;
}
  }