.proje{
    background-color: #F8F6F4;
    padding-top: 100px;
}
.cont{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding-bottom: 100px;
}

.co{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.co ul{
    display: flex;
    gap: 40px;
    font-size: 28px;
    font-weight: 600;
    color: white;
    list-style: none;
}
.co ul li{
    background: transparent;
    color: black;
    border: none;
}
.co ul li button{
    background: none;
    border: none;
    color: black;
    font-size: 28px;
    font-weight: 600;
}
.co ul li button:hover{
    font-size: 28px;
    font-weight: 600;
    color: var(--maincollor);
}
.co ul li button.active{
    font-size: 28px;
    font-weight: 600;
    color: var(--maincollor);
}
.cards1{
    display: flex;
    width: 100%;
    margin: 0;
    gap: 20px;;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.car{
    width: 20%;
    /* border: 12px solid #262626; */
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.card-head{
    box-shadow: 1px 2px 41px 0px rgba(0,0,0,0.83);
}
.card-head{
    width: 100%;
}
.card-head img{
    width: 100%;
    height: 100%;
}



@media (max-width: 992px) {


    .cards1{
        display: flex;
        width: 100%;
        margin: 0;
        gap: 20px;;
        justify-content: center;
        
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
    .car{
        width: 20%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }
    .card-head{
        box-shadow: 1px 2px 41px 0px rgba(0,0,0,0.83);
    }
    .card-head{
        width: 100%;
    }
    .card-head img{
        width: 100%;
    }
    .card-head video{
    max-width: 550px!important;
    height: 300px!important;
    
    }
    .co ul {
        display: flex;
        gap: 0px;
        flex-direction: column;
        align-items: center;
        padding: 0;

    }
    .cards1 {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .car {
        width: 85%;
    }
}
